import { defineComponent, onMounted, ref } from 'vue';
import { http } from '@/http';
import Toast from "vant/es/toast";
import "vant/es/toast/style/less";
import { CUSTOMER_ACT_LINK } from '@/plugins/acts-link';
export default defineComponent({
    name: 'CustomerActs',
    setup() {
        const actList = ref([]);
        const formatTime = (time) => {
            const timestamp = Date.parse(time.replace(/-/g, '/'));
            return (new Date(timestamp).getMonth() +
                1 +
                '月' +
                new Date(timestamp).getDate() +
                '日' +
                ' ' +
                (new Date(timestamp).getHours() < 10
                    ? '0' + new Date(timestamp).getHours()
                    : new Date(timestamp).getHours()) +
                ':' +
                (new Date(timestamp).getMinutes() < 10
                    ? '0' + new Date(timestamp).getMinutes()
                    : new Date(timestamp).getMinutes()));
        };
        const getActList = () => {
            http.post('/hbSeller/act/list', {}, false).then((res) => {
                if (res.code === '200') {
                    if (res.data) {
                        actList.value = res.data.map((item) => {
                            item.stime = formatTime(item.stime);
                            item.etime = formatTime(item.etime);
                            return item;
                        });
                    }
                    else {
                        actList.value = [];
                    }
                }
                else {
                    Toast.fail(res.msg);
                }
            });
        };
        onMounted(() => {
            getActList();
        });
        return {
            CUSTOMER_ACT_LINK,
            actList
        };
    }
});
